.file-reader {
    position: relative;
    height: 200px;
}

.form {
    width: 100%;
    height: 100%;
    position: relative;
}

.form__file {
    width: 100%;
    height: 100%;
}

.stub {
    border: 1px solid #000;
    border-radius: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    pointer-events: none;
    width: 100%;
    height: 100%;
    background: #fff;
    top: 0;
}

.file-wrapper {
    max-width: 1100px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.file-wrapper__img {
    width: 100%;
    margin-bottom: 1em;
    position: relative;
}

.img-box {
    position: relative;
    width: 200px;
}

.img-box__remove {
    position: absolute;
    top: 10px;
    right: 10px;
}
